import React, { FC } from 'react';
import { ItemClickType } from '@enum';
import { CommonProps } from '@interface';
import './index.less';

const Title: FC<CommonProps> = (props = {}) => {
    const {
        schema: {
            className = '',
            size = 'size-24', // 文案大小 size-16 | size-20 | size-24
            lineClamp = 1, // 一行or两行
            logInfo: { itemClickType = ItemClickType.CARD_TITLE_CLICK } = {},
        },
        style = {},
        info: { adTitle = '' },
    } = props;

    return adTitle ? (
        <div
            data-itemClickType={itemClickType}
            className={`title ${className} ${size} line-${lineClamp}`}
            style={style}
        >
            {adTitle}
        </div>
    ) : null;
};

export default Title;
